<template>
  <div :class="$root.themeHomeChange === '1' ? 'intelligence_light' : 'intelligence'">
    
    <div class="header">
      <div class="left">
        <img src="@/assets/img/companyCenter/jiantou.png" />
        <span class="headerTitle">{{title}}</span> 
      </div>
      
      <el-date-picker
        v-if="title == '智能监测'"
        v-model="date"
        size="mini"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        value-format="yyyy-MM-dd"
        style="width: 240px;"
        @change="datePickerChange"
      >
      </el-date-picker>
      
      <el-radio-group
        v-else
        v-model="timeCheck"
        @change="radioChange"
      >
        <el-radio :label="1">月</el-radio>
        <el-radio :label="2">季度</el-radio>
        <el-radio :label="3">年</el-radio>
      </el-radio-group>
    </div>
    
    <div class="content">
      
      <div v-if="title == '智能监测'" class="monitor">
        <div class="img" >
          <div class="bg-1"></div>
          <div class="bg-2"></div>
          <div class="bg-3"></div>
          <div class="bg-5"></div>
          <div class="spot-1 point"></div>
          <div class="spot-2 point"></div>
          <div class="spot-3 point"></div>
          <div class="spot-4 point"></div>
        </div>
        <div class="item-content">
          <div class="item" v-for="(item, index) in monitorList" :key="index">
            <p class="label" style="font-size: 16px">{{item.type}}</p>
            <el-row class="data">
              <el-col :span="12" class="total">
                {{item.sum}}
              </el-col>
              <el-col :span="12" class="proportion">
                <p>同比: {{item.yearOnYear}}%</p>
                <p>环比: {{item.ringRatio}}%</p>
              </el-col>
            </el-row>
          </div>
        </div>
      </div>
      
      <div v-else-if="title == '智能定级'" class="grading">
        <img
          class="img img-grading"
          :src=" $root.themeHomeChange === '1' ? jztUrl_light : jztUrl" 
        />
        <div class="item-content">
          <div v-for="(item, index) in gradingList" :key="index" class="item">
            <div class="line" :style="{ width: 85 * ((4 - index) / 4) + 'px', left: -( 85 * ((4 - index) / 4) + 10) + 'px' }"></div>
            <div :class="'title header-' + (index + 1)">
              <span>{{item.qxlx}}</span>
              <span>{{item.number}} 个</span>
              <span> 占比：{{item.zhanbi.toFixed(2)}} %</span>
            </div>
            <p class="proportion">
              同比: {{item.yearOnYear.toFixed(2)}}%
              环比: {{item.ringRatio.toFixed(2)}}%
            </p>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
  export default {
    name: "intelligence",
    props: {
      title: {
        type: String,
        default: () => {
          return '智能组件';
        },
      },
      data: {
        type: Array,
        default: () => {
          return [];
        },
      }
    },
    data(){
      return {
        jztUrl: require('@/assets/img/riskControlCenter/jzt.png'),
        jztUrl_light: require('@/assets/img/riskControlCenter/jzt_light.png'),
        date: '',
        timeCheck: 3,
        monitorList : [],
        gradingList : []
      }
    },
    mounted() {
      this.init();
    },
    created() {},
    watch: {},
    methods: {
      init() {
        // 初始化时间选择框数据
        let nowDate = new Date();
        let year = nowDate.getFullYear(); 
        let month = nowDate.getMonth() + 1;
        let day = nowDate.getDate();
        month = (month < 10 ? "0" + month : month);
        
        let yearFirstDay = nowDate.getFullYear() + '-01-01'
        
        this.date = [yearFirstDay, year + '-' + month + '-' + day];
      },
      // 单选切换
      radioChange(key) {
        this.$emit('gradingRadioChange', key);
      },
      // 时间切换
      datePickerChange(data) {
        this.$emit('monitorDateChange', data);
      }
    }
  }
</script>

